module.exports = "<h1 id=\"vuejs-监听参数\">Vue.js 监听参数</h1>\n<h3 id=\"写在前面\">写在前面</h3>\n<blockquote>\n<ol>\n<li>在<code>myBlog</code>项目中，要实现纯前端跳转，所以博客的markdown无法从后台获取，便需要一个数组来储存markdown文件。</li>\n<li>自己创建一个显示markdown的组件，可以根据传入的markdown自动编码。</li>\n<li>使用router.push将参数传进vue组件</li>\n</ol>\n</blockquote>\n<h3 id=\"遇到的问题\">遇到的问题</h3>\n<blockquote>\n<p>当vue $router的路径一致时，默认不进行操作，因此当参数不同，但路径相同时便没有数据的更新。</p>\n</blockquote>\n<pre><code>// Menu.vue\nthis.$router.push({path: &#39;/menu/myBlogs&#39;, query: {postIndex: row.index}})</code></pre><h3 id=\"解决方法\">解决方法</h3>\n<blockquote>\n<p>使用<code>watch</code>来监听参数的变化，如果变化，便变更绑定的markdown文件，并重新解析。</p>\n</blockquote>\n<pre><code>// Myblogs.vue\nwatch: {\n    &#39;$route.query.postIndex&#39; (val) { // 监听&quot;route.query&quot;中的&quot;postIndex&quot;参数\n        console.log(&#39;changed!__&#39; + val) // 在控制台输出变更后的值\n        var markedPost = this.blogs[val].blog // 提取markdown文件\n        this.compiledMarkdown = marked(markedPost) // 将markdown文件编码成HTML\n    }\n}</code></pre>";


